</template>

<script>
import FilterPanel from '@/components/filter-panel/index.vue'

export default {
  components: { FilterPanel },
  data() {
    return {
      tabs: [
        { value: 'summary', label: 'Âá∫Â∫ìÊ±áÊÄª', desc: 'ÈÄêÂçïÁªüËÆ°' },
        { value: 'detail', label: 'ËçØÊùêÊòéÁªÜ', desc: 'ÈÄêÊâπËÆ∞ÂΩï' },
        { value: 'period', label: 'Êó∂Èó¥ÊÆµÊòéÁªÜ', desc: '‰∏ÄÈîÆÊó∂Èó¥ÊÆµ' }
      ],
      activeTab: 'summary',
      loading: false,
      summaryData: null,
      detailData: null,
      periodData: null,
      filters: {
        startDate: '',
        endDate: '',
        drugName: '',
        operator: '',
        recordNo: '',
        batchNo: '',
        toLocation: '',
        quick: 'month',
        includeDraft: false
      },
      locationFilterIndex: 0,
      locationOptions: [
        { label: 'ÂÖ®ÈÉ®Âõ≠Âå∫', value: '' },
        { label: 'ÈôÜÂõ≠', value: 'land_park' },
        { label: 'Ê∞¥Âõ≠', value: 'water_park' }
      ],
      periodFilter: 'month',
      periodRange: {
        startDate: '',
        endDate: ''
      },
      quickFilters: [
        { label: '‰ªäÂ§©', value: 'today' },
        { label: 'Êú¨Âë®', value: 'week' },
        { label: 'Êú¨Êúà', value: 'month' },
        { label: 'Êú¨Â≠£Â∫¶', value: 'quarter' },
        { label: 'Êú¨Âπ¥', value: 'year' },
        { label: 'Ëá™ÂÆö‰πâ', value: 'custom' }
      ],
      periodQuickFilters: [
        { label: '‰ªäÂ§©', value: 'today' },
        { label: 'Ëøë7Â§©', value: 'week' },
        { label: 'Êú¨Êúà', value: 'month' },
        { label: 'Êú¨Â≠£Â∫¶', value: 'quarter' },
        { label: 'Êú¨Âπ¥', value: 'year' }
      ],
      userInfo: uni.getStorageSync('userInfo') || {}
    }
  },
  computed: {
    summaryRows() {
      return this.summaryData?.records || []
    },
    detailRows() {
      return this.detailData?.details || []
    },
    periodRows() {
      return this.periodData?.details || []
    },
    statistics() {
      if (this.activeTab === 'summary') return this.summaryData?.statistics || null
      if (this.activeTab === 'detail') return this.detailData?.statistics || null
      if (this.activeTab === 'period') return this.periodData?.statistics || null
      return null
    },
    hasData() {
      if (this.activeTab === 'summary') return !!(this.summaryRows.length)
      if (this.activeTab === 'detail') return !!(this.detailRows.length)
      if (this.activeTab === 'period') return !!(this.periodRows.length)
      return false
    }
  },
  created() {
    // ÈªòËÆ§ÊåâÂΩìÂâçÂø´Êç∑Á≠õÈÄâÂíåÊó∂Èó¥ÊÆµÂä†ËΩΩÊï∞ÊçÆ
    this.applyQuickRange(this.filters.quick || 'month')
    this.applyPeriodRange(this.periodFilter || 'month')
  },
  methods: {
    // È°∂ÈÉ®Ê†áÁ≠æÂàáÊç¢
    switchTab(value) {
      if (this.activeTab === value) return
      this.activeTab = value
      // Êó∂Èó¥ÊÆµÊ†áÁ≠æ‰ΩøÁî®Áã¨Á´ãÁöÑ periodRange
      if (value === 'period') {
        // ‰øùËØÅ periodRange Â∑≤Ê†πÊçÆÂΩìÂâç periodFilter ËÆæÁΩÆÂ•Ω
        this.applyPeriodRange(this.periodFilter || 'month')
      }
      this.fetchCurrentTab()
    },
    fetchCurrentTab() {
      if (this.activeTab === 'summary') {
        this.loadSummary()
      } else if (this.activeTab === 'detail') {
        this.loadDetail()
      } else {
        this.loadPeriod()
      }
    },
    async loadSummary() {
      this.loading = true
      try {
        const payload = this.buildBasePayload()
        const res = await this.$api.callFunction('reports', {
          action: 'outboundReport',
          data: payload
        })
        if (res?.success) {
          this.summaryData = res.data
        }
      } catch (err) {
        console.error('Âä†ËΩΩÊ±áÊÄªÂ§±Ë¥•', err)
      } finally {
        this.loading = false
      }
    },
    async loadDetail() {
      this.loading = true
      try {
        const payload = this.buildBasePayload()
        const res = await this.$api.callFunction('reports', {
          action: 'outboundDetailReport',
          data: payload
        })
        if (res?.success) {
          this.detailData = res.data
        }
      } catch (err) {
        console.error('Âä†ËΩΩÊòéÁªÜÂ§±Ë¥•', err)
      } finally {
        this.loading = false
      }
    },
    async loadPeriod() {
      this.loading = true
      try {
        const res = await this.$api.callFunction('reports', {
          action: 'outboundDetailReport',
          data: {
            startDate: this.periodRange.startDate,
            endDate: this.periodRange.endDate
          }
        })
        if (res?.success) {
          this.periodData = res.data
        }
      } catch (err) {
        console.error('Âä†ËΩΩÊó∂Èó¥ÊÆµÊòéÁªÜÂ§±Ë¥•', err)
      } finally {
        this.loading = false
      }
    },
    buildBasePayload() {
      return {
        startDate: this.filters.startDate,
        endDate: this.filters.endDate,
        drugName: this.filters.drugName,
        operator: this.filters.operator,
        recordNo: this.filters.recordNo,
        batchNo: this.filters.batchNo,
        toLocation: this.filters.toLocation || '',
        includeDraft: this.filters.includeDraft
      }
    },
    updateFilter(key, value) {
      this.filters[key] = value
    },
    onDateChange({ start, end }) {
      this.filters.startDate = start || ''
      this.filters.endDate = end || ''
      this.filters.quick = 'custom'
    },
    selectQuickFilter(value) {
      this.applyQuickRange(value)
      if (value !== 'custom') {
        this.fetchCurrentTab()
      }
    },
    applyQuickRange(value) {
      this.filters.quick = value
      const { start, end } = this.getRangeByQuick(value)
      this.filters.startDate = start
      this.filters.endDate = end
    },
    selectPeriodFilter(value) {
      if (this.periodFilter === value) return
      this.periodFilter = value
      this.applyPeriodRange(value)
      this.loadPeriod()
    },
    applyPeriodRange(value) {
      const { start, end } = this.getRangeByQuick(value)
      this.periodRange.startDate = start
      this.periodRange.endDate = end
    },
    getRangeByQuick(value) {
      const today = new Date()
      const format = d => this.formatDate(d)
      let start = new Date(today)
      let end = new Date(today)
      switch (value) {
        case 'today':
          break
        case 'week': {
          const day = start.getDay() || 7
          start.setDate(start.getDate() - (day - 1))
          break
        }
        case 'month':
          start = new Date(today.getFullYear(), today.getMonth(), 1)
          break
        case 'quarter': {
          const quarter = Math.floor(today.getMonth() / 3)
          start = new Date(today.getFullYear(), quarter * 3, 1)
          break
        }
        case 'year':
          start = new Date(today.getFullYear(), 0, 1)
          break
        default:
          return { start: this.filters.startDate, end: this.filters.endDate }
      }
      return {
        start: format(start),
        end: format(end)
      }
    },
    resetFilters() {
      this.filters.drugName = ''
      this.filters.operator = ''
      this.filters.recordNo = ''
      this.filters.batchNo = ''
      this.filters.toLocation = ''
      this.locationFilterIndex = 0
      this.filters.includeDraft = false
      this.applyQuickRange('month')
      this.fetchCurrentTab()
    },
    onLocationFilterChange(e) {
      const idx = Number(e.detail.value || 0)
      this.locationFilterIndex = idx
      this.filters.toLocation = this.locationOptions[idx].value
      this.fetchCurrentTab()
    },
    toggleIncludeDraft() {
      this.filters.includeDraft = !this.filters.includeDraft
      this.fetchCurrentTab()
    },
    viewDetail(id) {
      if (!id) return
      uni.navigateTo({
        url: `/pages-sub/out/detail?id=${id}`
      })
    },
    renderStatus(status) {
      const map = {
        draft: 'ËçâÁ®ø',
        pending_review: 'ÂæÖÂ§çÊ†∏',
        rejected: 'Â∑≤È©≥Âõû',
        completed: 'Â∑≤ÂÆåÊàê'
      }
      return map[status] || status || '-'
    },
    async exportExcel() {
      if (!this.hasData) {
        uni.showToast({ title: 'ÊöÇÊó†Êï∞ÊçÆÂèØÂØºÂá∫', icon: 'none' })
        return
      }
      try {
        uni.showLoading({ title: 'ÁîüÊàêExcel...', mask: true })
        const payload = this.activeTab === 'period'
          ? { startDate: this.periodRange.startDate, endDate: this.periodRange.endDate }
          : this.buildBasePayload()
        const res = await this.$api.callFunction('reports', {
          action: 'exportOutboundExcel',
          data: {
            ...payload,
            mode: this.activeTab === 'summary' ? 'summary' : 'detail',
            printUser: this.userInfo?.name || ''
          }
        })
        uni.hideLoading()
        if (res?.success && res.fileID && res.filename) {
          // Ëé∑Âèñ‰∫ë‰∏¥Êó∂‰∏ãËΩΩÈìæÊé•
          const urlRes = await wx.cloud.getTempFileURL({ fileList: [res.fileID] })
          const fileUrl = urlRes?.fileList?.[0]?.tempFileURL
          if (fileUrl) {
            // Ëá™Âä®‰∏ãËΩΩÂπ∂‰øùÂ≠òÂà∞‚ÄúÂåªÂä°ÂÆ§‚ÄùÊñá‰ª∂Â§π
            this.downloadAndSaveLocal(fileUrl, res.filename, 'Excel')
          } else {
            uni.showToast({ title: 'Ëé∑Âèñ‰∏ãËΩΩÈìæÊé•Â§±Ë¥•', icon: 'none' })
          }
        } else {
          uni.showToast({ title: 'ÁîüÊàêExcelÂ§±Ë¥•', icon: 'none' })
        }
      } catch (err) {
        uni.hideLoading()
        console.error('ÂØºÂá∫ExcelÂ§±Ë¥•:', err)
        uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' })
      }
    },
    async exportPDF() {
      if (!this.hasData) {
        uni.showToast({ title: 'ÊöÇÊó†Êï∞ÊçÆÂèØÂØºÂá∫', icon: 'none' })
        return
      }
      try {
        uni.showLoading({ title: 'ÁîüÊàêPDF...', mask: true })
        const payload = this.activeTab === 'period'
          ? { startDate: this.periodRange.startDate, endDate: this.periodRange.endDate }
          : this.buildBasePayload()
        const res = await this.$api.callFunction('reports', {
          action: 'exportOutboundPDF',
          data: {
            ...payload,
            mode: this.activeTab === 'summary' ? 'summary' : 'detail',
            printUser: this.userInfo?.name || ''
          }
        })
        uni.hideLoading()
        if (res?.success && res.fileID) {
          // Áî®‰∫ëÊñá‰ª∂IDÁîüÊàê‰∏¥Êó∂‰∏ãËΩΩÈìæÊé•
          const urlRes = await wx.cloud.getTempFileURL({ fileList: [res.fileID] })
          const fileUrl = urlRes?.fileList?.[0]?.tempFileURL
          // Ëá™Âä®‰∏ãËΩΩÂπ∂‰øùÂ≠òÂà∞‚ÄúÂåªÂä°ÂÆ§‚ÄùÁõÆÂΩï
          // ‰ªé‰∫ëpathÊé®Êñ≠pdfÊñá‰ª∂Âêç
          let filename = ''
          if (res.fileID) {
            const idParts = res.fileID.split('/')
            filename = idParts[idParts.length - 1] || `outbound_report_${+new Date()}.pdf`
          }
          if (fileUrl) {
            this.downloadAndSaveLocal(fileUrl, filename, 'PDF')
          } else {
            uni.showToast({ title: 'Ëé∑Âèñ‰∏ãËΩΩÈìæÊé•Â§±Ë¥•', icon: 'none' })
          }
        } else {
          uni.showToast({ title: 'ÁîüÊàêPDFÂ§±Ë¥•', icon: 'none' })
        }
      } catch (err) {
        uni.hideLoading()
        uni.showToast({ title: 'ÂØºÂá∫Â§±Ë¥•', icon: 'none' })
      }
    },
    // ÈÄöÁî®Êú¨Âú∞‰øùÂ≠òÊñπÊ≥ï
    downloadAndSaveLocal(fileUrl, filename, fileType) {
      const fs = wx.getFileSystemManager()
      const folder = `${wx.env.USER_DATA_PATH}`
      const savePath = `${folder}/${filename}`
      try {
        fs.mkdirSync(folder, true)
      } catch (e) {}
      uni.downloadFile({
        url: fileUrl,
        success: (res) => {
          if (res.statusCode === 200) {
            fs.saveFile({
              tempFilePath: res.tempFilePath,
              filePath: savePath,
              success: () => {
                const lower = (filename || '').toLowerCase()
                let fileTypeExt = 'xlsx'
                if (lower.endsWith('.pdf')) fileTypeExt = 'pdf'
                wx.openDocument({
                  filePath: savePath,
                  fileType: fileTypeExt,
                  showMenu: true,
                  fail: () => {
                    uni.showModal({
                      title: `${fileType}Êñá‰ª∂Â∑≤‰øùÂ≠ò`,
                      content: `Êñá‰ª∂Â∑≤‰øùÂ≠òÂà∞ÔºöÂæÆ‰ø°-Êàë-ÊúçÂä°-Â∞èÁ®ãÂ∫è-ÊàëÁöÑÊñá‰ª∂/${filename}\n\nËØ∑ÂâçÂæÄÂæÆ‰ø°-Êàë-ÊúçÂä°-Â∞èÁ®ãÂ∫è-ÊàëÁöÑÊñá‰ª∂ Êü•Áúã„ÄÅÂàÜ‰∫´ÊàñÂØºÂá∫„ÄÇ`,
                      showCancel: false,
                      confirmText: 'Áü•ÈÅì‰∫Ü'
                    })
                  }
                })
              },
              fail: () => {
                uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
              }
            })
          } else {
            uni.showToast({ title: '‰∏ãËΩΩÂ§±Ë¥•', icon: 'none' })
          }
        },
        fail: () => {
          uni.showToast({ title: 'Êñá‰ª∂‰∏ãËΩΩÂ§±Ë¥•', icon: 'none' })
        }
      })
    },
		printReport() {
			uni.showToast({
				title: 'ÊâìÂç∞ÂäüËÉΩÂºÄÂèë‰∏≠',
				icon: 'none'
			})
		},
		formatDate(date) {
      if (!date) return ''
      const d = new Date(date)
      if (Number.isNaN(d.getTime())) return ''
      const y = d.getFullYear()
      const m = String(d.getMonth() + 1).padStart(2, '0')
      const day = String(d.getDate()).padStart(2, '0')
      return `${y}-${m}-${day}`
		}
	}
}
